"use client";

import React, { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import ConfirmDeleteModal from "./ConfirmDeleteModal";
import { CompanyService } from "@/services/company.service";
import toast from "react-hot-toast";
import CategoryTypeFormModal from "./CategoryTypeFormModal";

/* =========================
   Types
========================= */
interface CategoryTypeDto {
  id: string;
  categoryId: string;
  categoryName: string;
  name: string;
  description: string;
   createdAt: string;
  createdBy: string;


}

/* =========================
   Action Menu
========================= */
function ActionMenu({
  openUp,
  onEdit,
  onDelete,
}: {
  openUp?: boolean;
  onEdit: () => void;
  onDelete: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    }
    if (open) document.addEventListener("mousedown", handleClickOutside);
    return () =>
      document.removeEventListener("mousedown", handleClickOutside);
  }, [open]);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full p-2 hover:bg-gray-100 dark:hover:bg-gray-700"
      >
        ‚ãÆ
      </button>

      {open && (
        <div
          className={`absolute right-0 z-50 w-36 rounded-lg border bg-white shadow-lg
          dark:border-gray-700 dark:bg-gray-900
          ${openUp ? "bottom-full mb-2" : "top-full mt-2"}`}
        >
          <button
            onClick={() => {
              onEdit();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm hover:bg-gray-50 dark:hover:bg-gray-800"
          >
            ‚úèÔ∏è Edit
          </button>

          <button
            onClick={() => {
              onDelete();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   Category Table
========================= */
export default function CategoryTypeTable() {
  const [categoryTypes, setCategoryTypes] = useState<CategoryTypeDto[]>([]);
  const [search, setSearch] = useState(""); 

  const [openModal, setOpenModal] = useState(false);
  const [mode, setMode] = useState<"add" | "edit">("add");
  const [selectedCategory, setSelectedCategory] = useState<CategoryTypeDto | null>(null);

  const [openDelete, setOpenDelete] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [loading, setLoading] = useState(false);

  /* =========================
     LOAD USERS
  ========================= */
async function loadCategoryType() {
  setLoading(true);
  try {
    const res = await CompanyService.getAllCategoryTypes();
    setCategoryTypes(res.data.data);
  } catch (err: any) {
    // ‚úÖ Read backend ResponseWrapper
    const apiResponse = err?.response?.data;

    if (apiResponse?.StatusCode === 403) {
      toast.error(apiResponse.Errors?.[0] || "Access denied");
    } else if (apiResponse?.StatusCode === 401) {
      toast.error("Please login to continue");
    } else {
      toast.error(apiResponse?.Message || "Failed to load Categories");
    }
  } finally {
    setLoading(false);
  }
}


  useEffect(() => {
    loadCategoryType();
  }, []);

  /* =========================
     ADD / UPDATE
  ========================= */
async function handleSubmit(data: {
  categoryId: string;
  name: string;
  description: string;
}) {
  try {
    const payload = {
      categoryId: data.categoryId,
      name: data.name,
      description: data.description,
    };

    if (mode === "add") {
      await CompanyService.createCategoryType(payload);
      toast.success("Category created successfully");
    } else {
      if (!selectedCategory?.id) {
        toast.error("Category ID missing");
        return;
      }
      alert("checked:"+payload);

      await CompanyService.updateCategoryType(selectedCategory.id, payload);
      toast.success("Category updated successfully");
    }

    setOpenModal(false);
    loadCategoryType();
  } catch (err: any) {
    toast.error(err?.response?.data?.message || "Operation failed");
  }
}


  /* =========================
     DELETE
  ========================= */
 async function confirmDelete() {
  if (!selectedCategory) return;

  try {
    setDeleting(true);
    await CompanyService.deleteCategory(selectedCategory.id);
    toast.success("Category deleted successfully");
    setOpenDelete(false);
    loadCategoryType();
  } catch {
    toast.error("Failed to delete category");
  } finally {
    setDeleting(false);
  }
}


  /* =========================
     FILTER
  ========================= */
  const filteredCategoryTypes = categoryTypes.filter(
    (u) =>
      u.name.toLowerCase().includes(search.toLowerCase()) 

  );

  return (
    <div className="w-full rounded-xl bg-white px-6 pb-6 dark:bg-gray-900">
      <h2 className="mb-4 text-xl font-semibold text-gray-800 dark:text-white">
        Categories
      </h2>

      {/* Top Bar */}
      <div className="mb-4 flex items-center justify-between">
        <Input
          placeholder="Search categories..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button
          size="sm"
          onClick={() => {
            setMode("add");
            setSelectedCategory(null);
            setOpenModal(true);
          }}
        >
          + Add Category
        </Button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50 dark:bg-gray-800">
            <tr className="text-left text-sm text-gray-600 dark:text-gray-300">
              <th className="px-3 py-3">Category Type</th>
            <th className="px-3 py-3">Description</th>
              <th className="px-3">Category</th>
              <th className="px-3">CreatedAt</th>
              <th className="px-3">CreatedBy</th>
              <th className="px-3 text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {loading ? (
              <tr>
                <td colSpan={7} className="py-6 text-center text-sm">
                  Loading categories...
                </td>
              </tr>
            ) : (
              filteredCategoryTypes.map((u, i) => (
                <tr
                  key={u.id}
                  className="border-t hover:bg-gray-50 dark:hover:bg-gray-800 transition"
                >
                  {/* BRANCH */}
                
                  {/* EMAIL */}
                  <td className="px-3 text-sm text-gray-700 dark:text-gray-300">
                    {u.name || "-"}
                  </td>

                  {/* PHONE */}
                  <td className="px-3 text-sm font-mono text-gray-700 dark:text-gray-300">
                    {u.description || "-"}
                  </td>

                  {/* ADDRESS */}
                  <td className="px-3 text-sm text-gray-600 dark:text-gray-400">
                    {u.categoryName || "-"}
                  </td>


            

                <td className="px-3 text-sm text-gray-600 dark:text-gray-400">
  {u.createdAt ? new Date(u.createdAt).toISOString().split("T")[0] : "-"}
</td>


                      <td className="px-3 text-sm text-gray-600 dark:text-gray-400">
                    {u.createdBy || "-"}
                  </td>


                  {/* ACTION */}
                  <td className="px-3 text-right">
                    <ActionMenu
                      openUp={i === filteredCategoryTypes.length - 1}
                      onEdit={() => {
                        setMode("edit");
                        setSelectedCategory(u);
                        setOpenModal(true);
                      }}
                      onDelete={() => {
                        setSelectedCategory(u);
                        setOpenDelete(true);
                      }}
                    />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* ADD / EDIT MODAL */}
<CategoryTypeFormModal
  open={openModal}
  mode={mode}
  initialData={
    selectedCategory
      ? {
          name: selectedCategory.name,
          description: selectedCategory.description,
          categoryId: selectedCategory.categoryId,
        }
      : undefined
  }
  onClose={() => setOpenModal(false)}
  onSubmit={handleSubmit}
/>


      {/* DELETE CONFIRM MODAL */}
      <ConfirmDeleteModal
        open={openDelete}
        loading={deleting}
        onClose={() => setOpenDelete(false)}
        onConfirm={confirmDelete}
      />
    </div>
  );
}
