"use client";
import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { CompanyService } from "@/services/company.service";
import toast from "react-hot-toast";


export interface CategoryTypeFormDataType {
  categoryId: string;
  name: string;
  description: string;
}

interface CategoryType {
  id: string;
  name: string;
}



interface Props {
  open: boolean;
  mode: "add" | "edit";
  initialData?: CategoryTypeFormDataType;
  onClose: () => void;
  onSubmit: (data: CategoryTypeFormDataType) => void;
}

const emptyForm: CategoryTypeFormDataType = {
  categoryId: "",
  name: "",
  description: "",
};

export default function CategoryTypeFormData({
  open,
  mode,
  initialData,
  onClose,
  onSubmit,
}: Props) {
  const [form, setForm] = useState<CategoryTypeFormDataType>(emptyForm);
  const [errors, setErrors] =
    useState<Partial<Record<keyof CategoryTypeFormDataType, string>>>({});
  const [loading, setLoading] = useState(false);

  const [categoryTypes, setCategoryTypes] = useState<CategoryType[]>([]);
const [loadingCategoryTypes, setLoadingCategoryTypes] = useState(false);




useEffect(() => {
  if (!open) return;

  const fetchCategoryTypes = async () => {
    try {
      setLoadingCategoryTypes(true);

      const res = await CompanyService.getAllCategory();

      // ‚úÖ CORRECT: use the array
      setCategoryTypes(res.data.data);

    } catch (err) {
      console.error("Failed to load category types", err);
      setCategoryTypes([]); // safety fallback
    } finally {
      setLoadingCategoryTypes(false);
    }
  };

  fetchCategoryTypes();
}, [open]);


useEffect(() => {
  setForm(
    mode === "edit" && initialData
      ? initialData
      : emptyForm
  );
  setErrors({});
}, [mode, initialData, open]);


  useEffect(() => {
    const esc = (e: KeyboardEvent) => e.key === "Escape" && onClose();
    document.addEventListener("keydown", esc);
    return () => document.removeEventListener("keydown", esc);
  }, [onClose]);



  const update = (k: keyof CategoryTypeFormDataType, v: string) =>
    setForm((p) => ({ ...p, [k]: v }));


const validate = () => {
  const e: typeof errors = {};

  if (!form.name.trim()) {
    e.name = "Category type Name is required";
    toast.error(e.name);
  }

  if (!form.categoryId.trim()) {
    e.categoryId = "Category Name is required";
    toast.error(e.categoryId);
  }

  if (!form.description.trim()) {
    e.description = "Description is required";
    toast.error(e.description);
  }

  setErrors(e);
  return Object.keys(e).length === 0;
};


  const submit = async () => {
    if (!validate() || loading) return;
    setLoading(true);
    await onSubmit(form);
    setLoading(false);
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div
        className="w-full max-w-3xl bg-white dark:bg-gray-900 rounded-xl shadow-xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header (Centered) */}
        <div className="relative p-5 border-b dark:border-gray-800">
          <h3 className="text-lg font-semibold text-center text-gray-900 dark:text-white">
            {mode === "add" ? "Add Category Type" : "Edit Category Type"}
          </h3>
          <button
            onClick={onClose}
            className="absolute right-5 top-1/2 -translate-y-1/2 text-2xl text-gray-500 hover:text-gray-700"
          >
            √ó
          </button>
        </div>

        {/* Body */}
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-6">
          <Field label="Category Type" required error={errors.name}>
            <Input value={form.name} onChange={(e) => update("name", e.target.value)} />
          </Field>

          <Field label="Description" required error={errors.description}>
            <Input value={form.description} onChange={(e) => update("description", e.target.value)} />
          </Field>

<Field label="Category " required error={errors.categoryId}>
  <select
    className="w-full rounded-md border px-3 py-2 dark:bg-gray-800"
    value={form.categoryId}
    onChange={(e) => update("categoryId", e.target.value)}
    disabled={loadingCategoryTypes}
  >
    <option value="">
      {loadingCategoryTypes ? "Loading category types..." : "Select category type"}
    </option>



    {categoryTypes.map((categoryType) => (
      <option key={categoryType.id} value={categoryType.id}>
        {categoryType.name}
      </option>
    ))}
  </select>
</Field>

        </div>

        {/* Footer */}
        <div className="p-5 border-t dark:border-gray-800 flex justify-end gap-3">
          <Button variant="outline" onClick={onClose}>Cancel</Button>
          <Button onClick={submit} disabled={loading}>
            {loading ? "Saving..." : mode === "add" ? "Create" : "Update"}
          </Button>
        </div>
      </div>
    </div>
  );
}

/* ---------- Small Helper ---------- */
function Field({
  label,
  children,
  error,
  required,
  full,
}: {
  label: string;
  children: React.ReactNode;
  error?: string;
  required?: boolean;
  full?: boolean;
}) {
  return (
    <div className={full ? "md:col-span-2" : ""}>
      <Label>
        {label} {required && <span className="text-red-500">*</span>}
      </Label>
      {children}
      {error && <p className="text-sm text-red-600 mt-1">{error}</p>}
    </div>
  );
}
